'use strict';
/**
 * @api SET_LOADING(text) SET_LOADING
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {string} url 目标URL地址
 * @apiDescription APP loading 显示
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * SET_LOADING()
 * @apiParamExample {string} 返回值
 * 无
 **/
import {WinJSDec, strError} from './APP/config';

var name = 'LOADING_SHOW';
var LOADING_SHOW = (text = '请稍后...') => {
  if (typeof text !== 'string') {
    throw new TypeError(`[${name}] ${strError}`);
  }
  WinJSDec(_ => {
    window.WinJSBridge.call('tools', 'showLoading', {text});
  });
};
export default LOADING_SHOW;